.header>.top_header {
    background: transparent url(../images/header_line.webp) repeat-x right top;
}
.from-work .header>.top_header {
    opacity: 0.2;
}

.header>.top_header>.game_header {
    background: transparent url(../images/header.webp) no-repeat center top;
    height: 100px;
}

@media only screen and (max-width: 680px) {
    .header>.top_header>.game_header {
        height: 70px;
        background-position: 55% 55%;
    }
}

.header>.t-navbar {
    background: rgb(239, 239, 239);
    background-image: linear-gradient(rgb(255, 255, 255), rgb(215 215 215));
    max-height: 45px;
    min-height: 45px;
    border-top: 1px solid silver;
    box-shadow: 0px 4px 4px 0px #b9b9b9;
    padding: 8px 0px;
    position: unset;
}
.from-work .header>.t-navbar {
    opacity: 0.3;
}

.header>.t-navbar .nav-link button {
    padding: 3px 5px;
}

.header>.t-navbar .nav-link {
    margin: 0px 8px;
    padding: 0;
}

@media only screen and (max-width: 680px) {
    .header>.t-navbar .nav-link {
        margin: 0px 4px;
        padding: 0;
    }
}

.header>.t-navbar img {
    user-select: none;
}

.header>.t-navbar .server-time {
    position: absolute;
    top: -1px;
    left: 1vw;
    z-index: 2;
    font-size: 14px;
    font-family: consolas, monospace;
    min-width: 70px;
    border: 1px solid gray;
    text-align: center;
    border-radius: 0px 0px 3px 3px;
    user-select: none;
    cursor: default;
    background-color: #444444;
    color: #FFFFFF;
}

.header>.t-navbar .server-number {
    position: absolute;
    top: -1px;
    right: 1vw;
    z-index: 2;
    font-size: 14px;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    min-width: 70px;
    border: 1px solid gray;
    text-align: center;
    border-radius: 0px 0px 3px 3px;
    user-select: none;
    cursor: default;
    background-color: #444444;
    color: #FFFFFF;
}

.server-time>svg {
    float: left;
}

.header>.t-navbar .header-items {
    /* position: relative; */
    display: block;
    text-align: center;
    min-width: 640px;
}

.header-items .header-images {
    filter: grayscale(1);
    /* opacity: 0.35; */
}

.header-items .header-images.active {
    filter: grayscale(0);
}

.header-items .header-images:hover {
    filter: grayscale(0);
}

.header>.t-navbar .header-items>* {
    display: unset;
}
.header image.header-images, .header svg.header-inbox {
    max-width: 80px;
    max-height: 80px;
}
@media only screen and (max-width: 680px) {
    .header>.t-navbar .header-items {
        min-width: 100%;
        text-align: center;
    }

    .header>.t-navbar>div>.mx-auto {
        margin: 0px !important;
        width: 100%;
    }

    .header img.header-images, .header svg.header-inbox {
        width: 65px;
        height: 65px;
    }

    .header>.t-navbar {
        max-height: 35px;
    }
}