.user-guid {
    cursor: default;
}

.user-guid ul>ul {
    margin-top: 5px;
    list-style-type: circle;
}

.user-guid.center-guid {
    text-align: center;
}

.user-guid .building-desc {
    margin-top: 15px;
    margin-bottom: 15px;
    /* text-align: right; */
}

.user-guid .building-cost img {
    margin-inline-start: 10px;
    position: relative;
    bottom: 3px;
}
.user-guid .building-cost img.clock {
    bottom: 2px;
}

.guid-footer {
    padding: 0.75rem;
    border-top: 1px solid #dee2e6;
    text-align: center;
}

