body {
  direction: rtl !important;
  text-align: right;
  position: relative;
  font-family: Tahoma, monospace, Geneva, Verdana, sans-serif;
  font-size: 14px;
}

img {
  -webkit-user-drag: none;
  -khtml-user-drag: none;
  -moz-user-drag: none;
  -o-user-drag: none;
  user-select: none;
}

a {
  text-decoration: none !important;
  font-weight: bold;
  color: #71D000;
}

a:hover {
  color: #00BC00;
}

a:active {
  color: #FF8000;
}

.middle-row {
  margin-top: 20px !important;
  min-height: 550px;
  padding-bottom: 5vh !important;
}


@media screen and (min-width: 961px) {
  .navi-drawer, .side-drawer {
    display: none;
  }

  .navi-col {
    background: transparent url(./images/shadow_a.png) no-repeat left top;
  }

  .side-col {
    background: transparent url(./images/shadow_b.png) no-repeat right top;
  }
}

@media screen and (max-width: 960px) {
  .middle-row {
    margin-top: 15px !important;
  }

  .navi-col, .side-col {
    position: absolute;
    width: 1px;
    z-index: 1;
    overflow-x: hidden;
    transition: 0.5s ease-in-out;
    height: 100%;
    background-color: white;
    background-image: none;
  }

  .navi-col {
    right: 0px;
    box-shadow: -2px 2px 5px 0px silver;
  }

  .side-col {
    left: 0px;
    box-shadow: 2px 2px 5px 0px silver;
  }

  .navi-drawer, .side-drawer {
    background-color: #739936;
    color: white;
    padding: 0px 5px;
    z-index: 2;
    direction: ltr;
    font-weight: bold;
    height: 36px;
    display: flex;
    align-items: center;
    width: 32px;
    position: absolute;
    justify-content: center;
    cursor: pointer;
    border: 1px solid silver;
    transition: 0.5s ease-in-out;
  }

  .navi-drawer {
    border-radius: 5px 0px 0px 5px;
    right: 0px;
    border-right-width: 0px;
  }

  .side-drawer {
    border-radius: 0px 5px 5px 0px;
    left: 0px;
    border-left-width: 0px;
  }
}

.middle-col {
  text-align: center;
}

.footer-row {
  background-color: #e7e7e7;
  width: 100%;
  padding: 5px;
  text-align: center;
  border-top: 1px solid silver;
}

.side_navi, .side_info, .content {
  margin-top: 20px;
}

#root>div {
  /* min-width: fit-content; */
  padding: 0;
}

#root>div>.row {
  margin: 0;
}

#root>div>.row>.col {
  padding: 0;
}